<template>
	<view class="container" v-if="isshow">
		<view class="myGroup">
			<text class="title">设置分组</text>
			<view class="addGrop" @click="addGrop">
				<uni-icons type="plusempty" size="12" color="#666"></uni-icons>
				<text>添加分组</text>
			</view>
		</view>
		<view class="groupBox">
			<checkbox-group class="channe_group" @change="checkboxChange">
				<u-swipe-action @open="open" v-for="(item, index) in list" :key="index" :index="index" :show="item.show" @click="groupTap"
				 :options="options">
					<view class="groupList">
						<text>{{item.label}}</text>
						<checkbox :value="item.value" :checked="item.checked" color="#03958C" style="transform:scale(0.7)" />
					</view>
				</u-swipe-action>
			</checkbox-group>
		</view>
		<!-- <view class="footer_flex">
			<text @click="addGrop">添加分组</text>
			<text @click="sureGrop">确定分组</text>
		</view> -->
		<view class="footer" @click="sureGrop">确定分组</text>
		</view>
		<!-- 添加/修改分组弹窗 -->
		<u-popup v-model="showPop" mode="center" width="600rpx" border-radius="14">
			<view class="switch_box">
				<view class="title">添加分组</view>
				<input type="text" v-model="groupName" placeholder="请填写分组名" />
				<view class="footerBtn">
					<text class="cancel" @click="showPop=false">取消</text>
					<text @click="surePop">确定</text>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				isshow: false,
				showModal: false, // 删除提示框
				showPop: false, // 添加分组弹窗
				groupName: '', // 分组名
				groupId: '', // 分组id
				cusId: '', // 客户id
				list: [],
				selectIds: [], // 选中的分组
				current: 0,
				show: false, // 左滑框
				options: [{
						text: '修改',
						style: {
							backgroundColor: '#03958C'
						}
					},
					{
						text: '删除',
						style: {
							backgroundColor: '#F5645A'
						}
					}
				]
			}
		},
		onLoad(options) {
			this.cusId = options.id
			this._getList()
		},
		methods: {
			// 获取分组列表
			_getList() {
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2013', Object.assign({}, {id: that.cusId}), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.list = res.data.data
					}
				})
			},
			// 添加分组
			addGrop() {
				this.groupName = ''
				this.showPop = true
			},
			// 多选
			checkboxChange(e) {
				this.selectIds = e.detail.value
			},
			// 确定添加/修改分组弹窗
			surePop() {
				let that = this
				if (that.groupId) { // 修改分组
					that.groupName = that.groupName
					let postData = {
						groupId: that.groupId,
						groupName: that.groupName
					}
					Tools.showLoading('修改中')
					httpUtils.sendHttpRequest('2015', Object.assign({}, postData), function(res) {
						Tools.hideLoading()
						uni.hideNavigationBarLoading()
						if (res.data.code == '0') {
							Tools.showSuccessToast('修改成功')
							setTimeout(function() {
								that.showPop = false
								that._getList()
							}, 500)
						}
					})
				} else { // 添加分组
					if(!that.groupName){
						Tools.showErrorToast('请填写分组名')
						return
					}
					Tools.showLoading('添加中')
					httpUtils.sendHttpRequest('2014', Object.assign({}, {groupName: that.groupName}), function(res) {
						Tools.hideLoading()
						uni.hideNavigationBarLoading()
						if (res.data.code == '0') {
							Tools.showSuccessToast('添加成功')
							setTimeout(function() {
								that.showPop = false
								that._getList()
							}, 500)
						}
					})
				}
			},
			// 侧滑效果点击
			groupTap(index, index1) {
				let that = this;
				if (index1 == 1) { // 删除
					that.close(index)
					uni.showModal({
						title: '提示',
						content: '确定删除该分组吗？',
						success: function(res) {
							if (res.confirm) {
								Tools.showLoading('删除中')
								httpUtils.sendHttpRequest('2016', Object.assign({}, {
									groupId: that.list[index].value
								}), function(res) {
									Tools.hideLoading()
									uni.hideNavigationBarLoading()
									if (res.data.code == '0') {
										Tools.showSuccessToast('删除成功')
										setTimeout(function() {
											that.showPop = false
											that._getList()
										}, 500)
									}
								})
							}
						}
					});
				} else { // 修改分组
					that.close(index)
					that.showPop = true
					that.groupName = that.list[index].label
					that.groupId = that.list[index].value
				}
			},
			// 侧滑显示隐藏
			open(index) {
				this.list[index].show = true;
				this.list.map((val, idx) => {
					if (index != idx) this.list[idx].show = false;
				});
			},
			close(index) {
				this.list[index].show = false;
			},
			// 确定选择分组
			sureGrop() {
				if(this.selectIds.length <= 0){
					Tools.showErrorToast('请选择分组')
					return
				}
				let that = this,
					postData = {
						id: that.cusId,
						value: that.selectIds.join(',')
					}
				Tools.showLoading('提交中')
				httpUtils.sendHttpRequest('2017', Object.assign({}, postData), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('操作成功')
						setTimeout(function() {
							uni.navigateBack({
								delta: 1
							})
						}, 500)
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		.myGroup {
			font-size: 28rpx;
			border-bottom: 1rpx solid #eee;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 1;
			background: #fff;
			display: flex;
			align-items: center;
			justify-content: space-between;
			.title{
				padding: 30rpx;
				font-weight: bold;
				font-size: 36rpx;
			}
			.addGrop{
				padding-right: 30rpx;
			}
		}
		// h5持有样式
		/* #ifdef H5 */
		.myGroup{
			top: 44px;
		}
		/* #endif */
		.groupBox {
			padding-bottom: 110rpx;
			padding-top: 100rpx;

			.channe_group {
				padding: 0 30rpx;

				.groupList {
					padding: 30rpx 0;
					border-bottom: 1rpx solid #eee;
					display: flex;
					justify-content: space-between;
					align-items: center;
				}
			}
		}

		// 弹窗样式
		.switch_box {
			position: relative;
			height: 300rpx;

			.title {
				font-size: 32rpx;
				text-align: center;
				padding: 20rpx 0;
				padding: 30rpx;
			}

			input {
				border: 2rpx solid #e5e5e5;
				padding: 10rpx;
				margin: 0 30rpx;
			}

			.footerBtn {
				width: 100%;
				position: absolute;
				bottom: 0;
				display: flex;
				justify-content: space-between;
				align-items: center;
				height: 90rpx;
				margin-top: 30rpx;
				border-top: 1rpx solid #e5e5e5;

				text {
					flex: 1;
					font-size: 32rpx;
					text-align: center;
					padding: 20rpx;
					color: #03958C;
				}

				.cancel {
					background-color: #fff;
					border-right: 1rpx solid #e5e5e5;
					color: #999;
				}
			}
		}
	}
</style>
